/* ---------------------------------------------- /*
* 1. General styles
/* ---------------------------------------------- */
//@import url(https://fonts.googleapis.com/css?family=Poppins:200,300,400,500,600,700,800,900);
@import "custom";

body {
 font: 400 12px/1.8 'Poppins', sans-serif;
 color: $text-color;
 //overflow-x: hidden;
}

img {
 max-width: 100%;
 height: auto;
}

a {
 color: #49e2d6;
}

a:hover {
 text-decoration: none;
 color: #999;
}

ul {
 list-style: none;
}

.list-custom li::before {
 content: "\2022";
 color: $primary-color;
 font-size: 1.5rem;
 width: 1em;
 margin-left: -1em;
 margin-right: 0.8rem;
 font-weight: bold;
 vertical-align: middle;
}

a, a:hover, a:active, a:visited, a:focus {
 text-decoration: none;
}

.bg-primary {
 background-color: $primary-color !important;
}

/*** Transition elements ***/

.form-control {
 -webkit-transition: all 0.4s ease-in-out 0s;
 -moz-transition: all 0.4s ease-in-out 0s;
 -ms-transition: all 0.4s ease-in-out 0s;
 -o-transition: all 0.4s ease-in-out 0s;
 transition: all 0.4s ease-in-out 0s;
}

.btn {
 -webkit-transition: all 0.125s ease-in-out 0s;
 -moz-transition: all 0.125s ease-in-out 0s;
 -ms-transition: all 0.125s ease-in-out 0s;
 -o-transition: all 0.125s ease-in-out 0s;
 transition: all 0.125s ease-in-out 0s;
}

/*** Reset box-shadow ***/

.btn,
.form-control,
.form-control:hover,
.form-control:focus,
.navbar-custom .dropdown-menu {
 -webkit-box-shadow: none;
 box-shadow: none;
}

/*** Typography ***/
h1, h2, h3, h4, h5, h6 {
 font: 700 40px/1.2 'Poppins', sans-serif;
 text-transform: uppercase;
 letter-spacing: 5px;
 margin: 0;
 color: $text-color;
}

h2 {
 font-size: 30px;
 margin: 0 0 30px;
}

h3 {
 font-size: 13px;
 letter-spacing: 1px;
 margin: 0 0 5px;
}

h4 {
 font-size: 12px;
 letter-spacing: 1px;
}

h5 {
 font-size: 12px;
 letter-spacing: 1px;
 font-weight: 400;
}

h6 {
 font-size: 11px;
 letter-spacing: 1px;
 font-weight: 400;
}

/*** Reset border-radius ***/

.btn,
.iconbox,
.progress,
#filter li,
.scroll-up a,
.form-control,
.input-group-addon {
 -webkit-border-radius: 3px;
 -moz-border-radius: 3px;
 -o-border-radius: 3px;
 border-radius: 3px;
}

.alert {
 border: 0;
}

/*** Progress Bar ***/

.progress,
.progress-bar {
 -webkit-box-shadow: none;
 box-shadow: none;
}

.progress-bar {
 background-color: #49e2d6;
}

/*** Tabs ***/

.nav-pills, .nav-tabs {
 margin-bottom: 20px;
}

.nav-pills > li > a {
 -webkit-border-radius: 3px;
 -moz-border-radius: 3px;
 -o-border-radius: 3px;
 border-radius: 3px;
}

.nav-tabs > li > a {
 -webkit-border-radius: 3px 3px 0 0;
 -moz-border-radius: 3px 3px 0 0;
 -o-border-radius: 3px 3px 0 0;
 border-radius: 3px 3px 0 0;
}

.nav-pills > li.active > a,
.nav-pills > li.active > a:hover,
.nav-pills > li.active > a:focus {
 background: #49e2d6;
}

/*** Panels ***/

.panel {
 border: 0;
}

.panel-body {
 background: #f5f5f5;
}

.panel-group .panel-heading + .panel-collapse > .panel-body {
 border: 0;
}

/*** Model ***/

.modal-content {
 border: 0;
}

/*** Labels ***/

.label {
 padding: .2em .6em .2em;
 font-size: 70%;
}

.label-custom-1 {
 background: #49e2d6;
}

.label-custom-2 {
 background: $text-color;
}

/*** Input-group ***/

.input-group .form-control {
 z-index: auto;
}

.input-group-addon {
 background: #D8E1E4;
 border: 2px solid #D8E1E4;
 padding: 6px 15px;
 font-weight: 700;
}

/*** Inputs styles ***/

.form-control {
 background: #fff none repeat scroll 0 0;
 border: 1px solid #ddd;
 font-size: 12px;
 padding: 0 15px;
}

.form-control:focus {
 border-color: $primary-color;
}

.input-lg,
.form-horizontal .form-group-lg .form-control {
 height: 38px;
 font-size: 13px;
}

.input-sm,
.form-horizontal .form-group-sm .form-control {
 font-size: 11px;
 height: 30px;
}

textarea.form-control {
 padding: 15px;
}

/*** Custom button style ***/

.btn {
 text-transform: uppercase;
 letter-spacing: 1px;
 font-size: 13px;
 padding: 8px 26px;
 border: 0;
}

.btn:hover,
.btn:focus,
.btn:active,
.btn.active {
 outline: inherit !important;
 background-color: $secondary-color !important;
 transition: background-color linear 0.2s;
}

.btn-list .btn {
 margin-bottom: 5px;
}

.btn-custom-1,
.btn-custom-2,
.btn-custom-3,
.btn-custom-4 {
 padding: 7px 25px;
}

.btn-custom-1 {
 background: #49e2d6;
 border: 1px solid #49e2d6;
 color: #fff;
}

.btn-custom-1:hover,
.btn-custom-1:focus,
.btn-custom-1:active,
.btn-custom-1.active {
 background: #fff;
 border-color: #fff;
 color: $text-color;
}

.btn-custom-2 {
 background: $primary-color; 
 border-radius: 3px;
 color: #fff;
}

.btn-custom-2:hover,
.btn-custom-2:focus,
.btn-custom-2:active,
.btn-custom-2.active {
 background: $primary-color;
 color: #fff;
 box-shadow: 0px 5px 20px rgba(0,0,0,0.1)
}

.btn-custom-3 {
 background: none;
 border: 1px solid $text-color;
 color: $text-color
}

.btn-custom-3:hover,
.btn-custom-3:focus,
.btn-custom-3:active,
.btn-custom-3.active {
 background: #49e2d6;
 border-color: #49e2d6;
 color: #FFF;
}

.btn-custom-4 {
 background: none;
 border: 1px solid $text-color;
 color: $text-color;
}

.btn-custom-4:hover,
.btn-custom-4:focus,
.btn-custom-4:active,
.btn-custom-4.active {
 background: $text-color;
 color: #FFF;
}
.btn-boutique-detail {
 border: 2px solid #b1a374;
 margin-bottom: 6px;
}
.boutique-status-size {
 font-size: 19pt;
}
.boutique-product-image {
 border-radius: 28px !important;
}
.boutique-product-description {
 border-radius: 20px !important;
}
#advancedTabContent {
 margin-top: -21px;
}
/* Button size */

.btn-lg,
.btn-group-lg > .btn {
 padding: 15px 50px;
 font-size: 1.2rem;
 border-radius: 100px;
 margin-right: 15px;
}

.btn-sm,
.btn-group-sm > .btn {
 padding: 6px 25px;
 font-size: 11px;
}

.btn-xs,
.btn-group-xs > .btn {
 padding: 5px 25px;
 font-size: 10px;
}

.btn {
 .icon-before {
 margin-right: 6px;
 }

 .icon-after {
 margin-left: 6px;
 }
}

/* ---------------------------------------------- /*
* Main
/* ---------------------------------------------- */

.module {
 padding: 80px 0 70px;
}

.module-gray {
 background: #f5f5f5;
}

.module-white {
 background: #fff;
}

.module-image {
 padding: 0;
}

.callout h1,
.callout h2,
.callout h3,
.callout h4,
.callout h5,
.callout h6,
.module-image,
.module-image h1,
.module-image h2,
.module-image h3,
.module-image h4,
.module-image h5,
.module-image h6 {
 color: #fff;
}

.paddingb-none {
 padding-bottom: 0;
}

.module-header {
 margin: 0 auto 50px;
 text-align: center;
}

.module-header-left {
 text-align: left;
}

.module-header-left .module-line {
 width: inherit;
 margin: 30px 0;
}

.module-subtitle {
