}

.navbar-custom .dropdown-menu > li > a {
 padding: 10px 20px;
}

.navbar-custom .nav .open > a,
.navbar-custom .dropdown-menu > li > a:hover,
.navbar-custom .dropdown-menu > li > a:focus {
}

.navbar-custom .navbar-toggle .icon-bar {
 background: $text-color;
}

.navbar-nav > li > a {
 padding-top: 20px;
 padding-bottom: 20px;
 position: relative;
}


/* ---------------------------------------------- /*
* 4. Portfolio
/* ---------------------------------------------- */

/* Filter */

#filter {
 list-style-type: none;
 text-align: center;
 margin: 0 0 40px 0;
 padding: 0;
}

#filter li {
 background: #f5f5f5;
 display: inline-block;
 font: 400 13px/1.2 'Poppins', sans-serif;
 text-transform: uppercase;
 text-decoration: none;
 letter-spacing: 1px;
 padding: 8px 20px;
 margin: 5px;
 color: $text-color;
 cursor: pointer;
 -webkit-transition: all 0.4s ease-in-out 0s;
 -moz-transition: all 0.4s ease-in-out 0s;
 -ms-transition: all 0.4s ease-in-out 0s;
 -o-transition: all 0.4s ease-in-out 0s;
 transition: all 0.4s ease-in-out 0s;
}

#filter li:hover,
#filter li.active {
 background: $primary-color;
 color: #fff;
}

/* Grid and portfolio items */

.list-items-container {
 position: relative;
 display: block;
 width: 100%;
 padding: 0;
 margin: 0;
}

.list-items-container .list-item {
 display: block;
 list-style: outside none none;
 margin-bottom: 20px;
 padding: 0 10px;
 width: 33.333%;
}

.list-items-container .list-item img {
 display: block;
 width: 100%;
 height: auto;
 transition: all 0.3s ease 0s;
}

.list-items-container .list-item {
 -webkit-transition: opacity 0.35s ease 0s, transform 0.35s ease 0s;
 -moz-transition: opacity 0.35s ease 0s, transform 0.35s ease 0s;
 -ms-transition: opacity 0.35s ease 0s, transform 0.35s ease 0s;
 -o-transition: opacity 0.35s ease 0s, transform 0.35s ease 0s;
 transition: opacity 0.35s ease 0s, transform 0.35s ease 0s;
}

figure {
 margin: 0;
 overflow: hidden;
 position: relative;
 border-radius: 5px;
}

.list-items-container .list-item figcaption {
 background: rgba(0,0,0,0.2);
 bottom: 0;
 left: 0;
 opacity: 0;
 padding: 13px;
 position: absolute;
 right: 0;
 text-align: center;
 top: 0;
 transition: all 0.3s ease 0s;
}

.list-items-container .list-item a {
 font-size: 0;
 height: 100%;
 left: 0;
 opacity: 0;
 position: absolute;
 text-indent: 200%;
 top: 0;
 width: 100%;
 z-index: 1000;
}

.list-items-container .caption-inner {
 position: absolute;
 left: 0;
 top: 50%;
 width: 100%;
 margin-top: -20px;
}

.caption-inner .portfolio-item-title {
 color: #fff;
 font-size: 16px;
 font-weight: 400;
}

.caption-inner .portfolio-item-desc {
 color: #ddd;
 font-size: 13px;
 font-style: italic;
}

#portfolio .list-items-container .list-item:hover img {
 filter: blur(6px);
}

.list-items-container .list-item:hover figcaption {
 opacity: 1;
}

/* Ajax portfolio */

.white-popup-block {
 position: relative;
 background: #fff;
 text-align: left;
 max-width: 940px;
 margin: 40px auto;
}

.popup-content,
.popup-callout {
 padding: 60px 40px;
}

.popup-callout {
 background: #f5f5f5;
 text-align: center;
}

.popup-header {
 text-align: center;
 margin: 0 0 30px;
}

.popup-title {
 margin: 0 0 5px;
}

.popup-subtitle {
 font-style: italic;
 font-size: 14px;
}

.popup-line {
 background: #49e2d6;
 width: 55px;
 height: 1px;
 margin: 15px auto;
}

.mfp-close-btn-in .mfp-close {
 color: #fff;
}

.portfolio-slider .owl-controls .owl-buttons div {
 position: absolute;
 top: 50%;
 height: 40px;
 width: 40px;
 line-height: 40px;
 padding: 0;
 font-size: 16px;
}

.portfolio-slider img {
 width: 100%;
}

.portfolio-slider .owl-controls {
 margin: 0;
}

.portfolio-slider .owl-controls .owl-buttons .owl-prev {
 left: 0;
 margin: -20px 0 0 0;
 -webkit-border-radius: 0 3px 3px 0;
 -moz-border-radius: 0 3px 3px 0;
 -o-border-radius: 0 3px 3px 0;
 border-radius: 0 3px 3px 0;
}

.portfolio-slider .owl-controls .owl-buttons .owl-next {
 right: 0;
 margin: -20px 0 0 0;
 -webkit-border-radius: 3px 0 0 3px;
 -moz-border-radius: 3px 0 0 3px;
 -o-border-radius: 3px 0 0 3px;
 border-radius: 3px 0 0 3px;
}


/* ---------------------------------------------- /*
* 5. Servise
/* ---------------------------------------------- */

.iconbox {
 background: #fff none repeat scroll 0 0;
 padding: 50px 20px;
 position: relative;
 text-align: center;
 max-width: 340px;
 margin: 30px auto;
 box-shadow: -6px 10px 40px rgba(0,0,0,0.06);
}

.iconbox-icon {
 color: $primary-color;
 position: absolute;
 left: 0;
 right: 0;
 top: -35px;
 height: 80px;
 width: 80px;
 margin: 0 auto;
 background: #fff;
 display: block;
 line-height: 80px;
 border-radius: 50px;
 font-size: 30px;
 box-shadow: -6px 10px 40px rgba(0,0,0,0.06);
}

.iconbox-text {
 padding-top: 30px;
}

.iconbox-title {
 margin: 0 0 15px;
 padding: 0;
}

/* ---------------------------------------------- /*
* 6. About
/* ---------------------------------------------- */

.margin-0 {
 margin: 0 !important;
}

.position-relative {
 position: relative;
}

.side-image-wrapper {
 position: relative;
 display: block;
 margin: 0 auto;
 margin-bottom: 35px;
}

.side-image-wrapper:after {
 content: "";
 position: absolute;
 height: 100%;
 width: 100%;
 border-radius: 69% 37% 50% 50%/70% 68% 32% 30%;
 background-color: #f5f5f5;
 top: 10%;
 left: 10%;
 z-index: -1;
 max-width: 500px;
}

.side-image img {
 border-radius: 69% 37% 50% 50%/70% 68% 32% 30%;
 height: auto;
 object-fit: cover;
 width: auto;
 margin: 0 auto;
 display: block;
}

.side-image-text {
 padding: 60px 60px 50px;
}

.iconboxleft {
 position: relative;
 margin: 0 0 50px;
}

.iconboxleft-icon {
 position: absolute;
 width: 55px;
 left: 0;
 top: 0;
 font-size: 32px;
 line-height: 1;
 color: $primary-color;
}

.iconboxleft-text {
 padding-left: 55px;
}

.iconboxleft-title {
 margin: 0 0 15px;
 padding: 0;
}


/* ---------------------------------------------- /*
* 7. Testimonial
/* ---------------------------------------------- */


.testimonials {
 margin-bottom: 20px;
 display: block;
}

.single-testimonials {
 max-width: 500px;
 margin: 0 auto;
}

.single-testimonials p {
 font-size: 13px;
}

.single-testimonials span {
 font-weight: 700;
 font-size: 12px;
}

.owl-theme .owl-controls {
 margin-top: 40px;
}

.owl-theme .owl-controls .owl-page span {
 height: 7px;
 width: 7px;
 background-color: rgba(0,0,0,0.2);
 margin: 3px;
 transition: all 0.2s linear;
}

.owl-theme .owl-controls .owl-page.active span, .owl-theme .owl-controls.clickable .owl-page:hover span {
 background-color: $primary-color;
 width: 20px
}

/* ---------------------------------------------- /*
* 8. Blog
/* ---------------------------------------------- */

.list-items-container .list-item .blogList, .blogList {
 position: relative;
 font-size: inherit;
 text-indent: 0;
}

.blogList {
 background: #fff none repeat scroll 0 0;
 position: relative;
 max-width: 340px;
 margin: 30px auto;
 box-shadow: -6px 10px 40px rgba(0,0,0,0.06);
 display: block;
 color: $text-color !important;
 transition: all 0.3s ease 0s;
 position: relative;
 font-size: inherit;
 text-indent: 0;
 border-radius: 5px;
 overflow: hidden;
}

.blogList-desc {
 padding: 25px 20px;
}

.blogList-img {
 position: relative;
}

.blogList-img img, .blog-wrapper.list-items-container .list-item img {
 height: 240px;
 width: 100%;
 object-fit: cover;
}

.blogList-img span {
 position: absolute;
 right: 10px;
 bottom: 10px;
 background: #fff;
 padding: 3px 10px;
 border-radius: 3px;
 box-shadow: -6px 10px 40px rgba(0,0,0,0.06);
 color: #429f98;
 transition: all 0.5s ease 0s;
 text-transform: capitalize;
}

.blogList:hover {
 margin-top: 25px;
}

.blogList:hover .blogList-img span {
 background: $primary-color;
 color: #fff;
}

.blogList-desc h3 {
 margin-bottom: 15px;
}

.blog-viewMore, .hireMe-btn {
 display: block;
 font: 400 13px/1.2 'Poppins', sans-serif;
 text-transform: uppercase;
 text-decoration: none;
 letter-spacing: 1px;
 padding: 10px 20px;
 margin: 5px;
 cursor: pointer;
 -webkit-transition: all 0.4s ease-in-out 0s;
 -moz-transition: all 0.4s ease-in-out 0s;
 -ms-transition: all 0.4s ease-in-out 0s;
 -o-transition: all 0.4s ease-in-out 0s;
 transition: all 0.4s ease-in-out 0s;
 background: $primary-color;
 color: #fff;
 max-width: 170px;
 margin: 10px auto;
 text-align: center;
 border-radius: 5px;
 width: 100%;
 border: none;
}

.hireMe-btn:hover, .btn-next-dates-aroma {
 box-shadow: 0px 10px 20px rgba(0,0,0,0.1);
 color: #fff;
}

.blog-items-container {
 position: relative;
 display: block;
 width: 100%;
 padding: 0;
 margin: 0;
}

.blog-item {
 display: block;
 list-style: outside none none;
 margin-bottom: 20px;
 overflow: hidden;
 padding: 0 10px;
 width: 33.333%;
 -webkit-transition: opacity 0.35s ease 0s, transform 0.35s ease 0s;
 -moz-transition: opacity 0.35s ease 0s, transform 0.35s ease 0s;
 -ms-transition: opacity 0.35s ease 0s, transform 0.35s ease 0s;
 -o-transition: opacity 0.35s ease 0s, transform 0.35s ease 0s;
 transition: opacity 0.35s ease 0s, transform 0.35s ease 0s;
}

.list-items-container.blog-wrapper .list-item {
 margin-bottom: 0;
}

.blog-detail-contents {
 max-width: 800px;
 margin: -100px auto 0px;
 background: #fff;
 display: block;
 padding: 30px;
 z-index: 11;
 position: relative;
 border-radius: 10px;
}

.blog-detail-content p {
 margin: 15px 0;
}

.blog-detail-content h3 {
 margin: 20px 0;
}

.blog-detail-content blockquote {
 background: #f5f5f5;
 border-color: $primary-color;
 padding: 5px 10px;
 border-radius: 4px;
 border-left: 5px solid $primary-color;
 font-style: italic;
}

.blog-detail-content img {
 width: 100%;
 margin: 15px 0;
 height: 350px;
 object-fit: cover;
}

.blog-detail-about .media-body {
 width: auto;
}

.blog-detail-about .media-body p {
 margin: 0;
}

.blog-detail-about .media {
 margin-bottom: 20px;
}

.blog-detail-about .media-left {
 margin-right: 10px;
}

.blog-detail-about img.media-object {
 border-radius: 5px;
 height: 80px;
 width: 80px; 
}

.media-body, .media-left, .media-right {
 vertical-align: middle;
}

.blog-detail-category {
 padding: 3px 15px;
 color: #fff;
 text-transform: capitalize;
 background: $primary-color;
 border-radius: 2px;
 margin: 15px 0;
}

.blog-detail-banner img {
 height: 400px;
 width: 100%;
 object-fit: cover;
}

.blog-detail-tags ul {
 margin: 0;
 padding: 0;
}

.blog-detail-tags ul li {
 display: inline-block;
}

.tags {
 display: inline-block;
 padding: 4px 10px;
 background-color: #f5f5f5;
 color: $text-color;
 margin-right: 5px;
 text-transform: capitalize;
 margin-bottom: 10px;
}

.tags:hover {
 background-color: $primary-color;
 color: #ffffff;
}

.blog-detail-sib {
 padding: 40px 0 10px;
}

.blog-detail-sib .blog-nav span {
 display: block;
 color: $text-color;
}

.blog-detail-sib .blog-nav {
 margin-bottom: 15px;
}

.blog-detail-sib .blog-nav p {
 font-size: 18px;
 color: $text-color;
 margin: 0;
}

.blog-nav {
 padding: 10px;
 background: #f5f5f5;
 transition: all 0.125s ease-in-out 0s;
 display: block;
 border-radius: 3px;
}

.blog-nav:hover {
 background: $primary-color;
 color: #fff;
}

.blog-nav:hover p, .blog-nav:hover span {
 color: #fff;
}

/* ---------------------------------------------- /*
* 9. Contact and Map
/* ---------------------------------------------- */

#map-section {
 position: relative;
 height: 450px;
 width: 100%;
}

#map {
 height: 450px;
 width: 100%;
}

.ajax-response {
 text-align: center;
}

/* ---------------------------------------------- /*
* 10. Footer
/* ---------------------------------------------- */

#footer {
 text-align: center;
 padding: 40px 0;
 background-color: #f5f5f5;
 color: $footer-text-color;

 .footer-logo {
 letter-spacing: 3px;
 color: $footer-text-color;
 font-size: 18px;
 text-transform: uppercase;
 font-weight: 700;
 margin-bottom: 20px;
 display: block;
 }

 .copyright {
 margin: 0;
 color: $text-color;
 visibility: inherit !important;
 }

 .social-links {
 list-style: none;
 padding: 0;
 margin: 0 0 20px;
 }

 .social-links li {
 display: inline-block;
 margin: 5px 10px;
 }

 .social-links a {
 display: block;
 text-align: center;
 font-size: 13px;
 color: rgba(255, 255, 255, .5);
 }

 .social-links a:hover {
 color: rgba(255, 255, 255, 1);
 }

 h5 {
 color: $footer-title-color;
 font-family: $font-footer !important;
 padding-bottom: 1.3rem;
 font-size: 1.1rem;
 }
}





/* ---------------------------------------------- /*
* 11. Scroll to top
/* ---------------------------------------------- */

.scroll-up {
 position: fixed;
 display: none;
 z-index: 999;
 bottom: 1.8em;
 right: 1.8em;
}

.scroll-up a {
 background-color: rgba(0, 0, 0, 0.3);
 display: block;
 width: 28px;
 height: 28px;
 text-align: center;
 color: #fff;
 font-size: 14px;
 line-height: 30px;
 cursor: pointer;
}

.scroll-up a:hover,
.scroll-up a:active {
 background-color: rgba(235, 235, 235, .8);
 color: $text-color;
}


/* ---------------------------------------------- /*
* 12. Preloader
/* ---------------------------------------------- */

#preloader {
 background: #FFF;
 bottom: 0;
 left: 0;
 position: fixed;
 right: 0;
 top: 0;
 z-index: 9999;
}

#status,
.status-mes {
 background-position: center;
 background-repeat: no-repeat;
 height: 200px;
 left: 50%;
 margin: -100px 0 0 -100px;
 position: absolute;
 top: 50%;
 width: 200px;
}

.status-mes {
 background: none;
 left: 0;
 margin: 0;
 text-align: center;
 top: 65%;
}

#loading {
 background: $loading-color;
 background: -moz-linear-gradient(45deg, $loading-color 0%, $primary-color 100%);
 background: -webkit-gradient(left bottom, right top, color-stop(0%, $loading-color), color-stop(100%, $primary-color));
 background: -webkit-linear-gradient(45deg, $loading-color 0%, $primary-color 100%);
 background: -o-linear-gradient(45deg, $loading-color 0%, $primary-color 100%);
 background: -ms-linear-gradient(45deg, $loading-color 0%, $primary-color 100%);
 background: linear-gradient(45deg, $loading-color 0%, $primary-color 100%);
 filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#fa1f55', endColorstr='#fd735a', GradientType=1 );
 height: 100%;
 width: 100%;
 position: fixed;
 z-index: 1;
 margin-top: 0px;
 top: 0px;
 z-index: 9999;
}

#loading-center {
 width: 100%;
 height: 100%;
 position: relative;
}

#loading-center-absolute {
 position: absolute;
 left: 50%;
 top: 50%;
 height: 100px;
 width: 50px;
 margin-left: -25px;
 transform: translateX(-50%);
}


.blobs {
 filter: url("#goo");
 padding: 50%;
}

.blob {
 background: #fff;
 left: 50%;
 top: 50%;
 width: 50px;
 height: 50px;
 border-radius: 100%;
 margin-top: -50px;
}

.blob:nth-child(2) {
 width: 40px;
 height: 40px;
 top: 5px;
 position: relative;
 left: -50px;
 animation: blobBounce 2s ease-in-out 0s infinite;
 z-index: -1;
}

@keyframes blobBounce {
 50% {
 left: 60px;
 }

 100% {
 left: -50px;
 }
}


/* ---------------------------------------------- /*
* 13. Tags
/* ---------------------------------------------- */

.tags-wrapper h1, .tags-wrapper h2 {
 font-weight: 300;
}

.tags-wrapper h2 {
 padding: 20px 0;
 margin: 0;
}

.tags-wrapper ul, .tags-wrapper li {
 padding: 0;
 list-style: none;
}

.article {
 padding: 20px;
}

.article .article-header {
 padding-bottom: 30px;
}

.tags-wrapper .posts-list {
 padding-bottom: 20px;
}

.posts-list li {
 font-size: 16px;
}

.posts-list li span {
 font-size: 13px;
}

.recent-wrapper {
 padding: 20px !important;
}

.recent-posts {
}


/* ---------------------------------------------- /*
* 14. All Media Queries
/* ---------------------------------------------- */

@media (max-width: 1200px) {
 .navbar-custom .navbar-brand,
 .navbar-custom .navbar-nav > li > a {
 font-size: 11px;
 letter-spacing: 1px;
 color: $text-color;
/* padding: 20px 9px !important;*/
 }

 .navbar-custom .navbar-brand, .navbar-custom .navbar-nav > li > a, .navbar-custom .navbar-nav .dropdown-menu > li > a {
 display: inline-block;
 }
}

@media (min-width: 767px) and (max-width: 991px) {

 .nav > li > a {
 padding-left: 10px;
 padding-right: 10px;
 }
}


@media (max-width: 991px) {

 .side-image {
 position: relative;
 }

 .list-items-container .list-item {
 width: 50%;
 }

 .white-popup-block {
 margin: 80px auto;
 }

 button.mfp-close {
 position: absolute;
 right: 50%;
 margin-right: -24px;
 top: -60px;
 }

 .mfp-close:active {
 top: -59px;
 }

 .blog-detail-contents {
 margin: 0;
 padding: 30px 0;
 }
}

@media (max-width: 767px) {

 .module {
 padding: 80px 0;
 }

 .list-items-container .list-item {
 width: 100%;
 margin-bottom: 30px;
 }

 .callout {
 text-align: center;
 }

 .side-image-text {
 padding: 50px 15px 0;
 }

 .mouse-icon {
 display: none;
 }

 .module-title span {
 font-size: 40px;
 top: -10px;
 }

 .iconbox {
 margin: 30px auto 40px;
 }

 .carousel-control {
 display: none;
 }

 .side-image-wrapper:after {
 top: 10%;
 left: 30%;
 height: 100%;
 width: 50%;
 }

 /*.navbar-custom,.navbar-custom .dropdown-menu {
 padding: .5rem 1rem;
 } */

 .dropdown-menu {
 padding-left: 2rem !important;
 }

 .intro h1 {
 font-size: 40px;
 }

 .side-image img {
 height: 50%;
 width: 50%;
 }

 #services .col-md-6:last-child .iconbox {
 margin-bottom: 0;
 }

 .blogList {
 margin: 20px auto;
 }

 .blog-detail-banner img {
 height: 250px;
 }

 .blog-detail-contents h2 {
 font-size: 20px;
 margin-bottom: 20px;
 }

 .blog-detail-about .media {
 margin-bottom: 0;
 }

 .blog-detail-content img {
 height: 300px;
 }
}

@media (max-width: 480px) {

 h1 {
 font-size: 34px;
 letter-spacing: 3px;
 }

 h2 {
 font-size: 24px;
 margin: 0 0 30px;
 letter-spacing: 3px;
 }
}
