/* Share Bar */
#share-bar {
 font-size: 1.4rem;
 background-color: rgb(252, 249, 249);
 padding: 24px;
 padding-bottom: 12px;
}

/* Title */
#share-bar .share-title {
 margin-bottom: 10px;
 font-weight: 500;
 font-size: 1.0rem;
 text-transform: uppercase;
 margin: 0;
}

/* All buttons */
.share-buttons {
 text-align: center;

 /* Facebook button */
 .fa-facebook-f {
 color: #3b5998;
 }
 .fa-facebook-f:hover {
 background-color: #3b5998;
 }

 /* Twitter button */
 .fa-twitter {
 color: #55acee;
 }
 .fa-twitter:hover {
 background-color: #55acee;
 }

 /* Google-PLus button */
 .fa-google-plus {
 color: #dd4b39;
 }
 .fa-google-plus:hover {
 background-color: #dd4b39;
 }

 /* Pinterest button */
 .fa-pinterest-p {
 color: #cb2027;
 }
 .fa-pinterest-p:hover {
 background-color: #cb2027;
 }

 /* Tumblr button */
 .fa-tumblr {
 color: #32506d;
 }
 .fa-tumblr:hover {
 background-color: #32506d;
 }

 /* Reddit button */
 .fa-reddit-alien {
 color: #ff4500;
 }
 .fa-reddit-alien:hover {
 background-color: #ff4500;
 }

 /* LinkedIn button */
 .fa-linkedin {
 color: #007bb5;
 }
 .fa-linkedin:hover {
 background-color: #007bb5;
 }

 /* Email button */
 .fa-envelope {
 color: #444444;
 }
 .fa-envelope:hover {
 background-color: #444444;
 }
}

/* Each button */
.share-button {
 width: 2.8rem;
 height: 2.8rem;
 text-align: center;
 margin: 0px;
 padding: 10px;
 margin-bottom: 10px;
 margin-right: 3px;
 border: 1px solid #D3D6D2;
 border-radius: 50%;
 background-color: white;

 box-shadow: inset 0 0 20px rgba(255, 255, 255, 0);
 outline: 1px solid;
 outline-color: rgba(255, 255, 255, .5);
 outline-offset: 0px;
 text-shadow: none;
 transition: all 500ms cubic-bezier(0.19, 1, 0.22, 1);
}
.share-button:hover {
 opacity: 1;
 color: #ffffff;
 border: 1px solid;
 box-shadow: inset 0 0 20px rgba(255, 255, 255, .5), 0 0 20px rgba(255, 255, 255, .2);
 outline-color: rgba(255, 255, 255, 0);
 outline-offset: 15px;
}

.tooltip {
 &> .tooltip-inner {
 font-size: 18px;
 background-color: $secondary-color;
 border-radius:.25rem;
 padding:3px 12px;
 box-shadow: 0px 3px 5px rgba(0,0,0,0.1) !important;
 }
}

.bs-tooltip-auto[x-placement^=bottom] .arrow::before, .bs-tooltip-bottom .arrow::before {
 border-bottom-color: $secondary-color !important;
}
.bs-tooltip-auto[x-placement^=top] .arrow::before, .bs-tooltip-top .arrow::before {
 border-top-color: $secondary-color !important;
}
.bs-tooltip-auto[x-placement^=left] .arrow::before, .bs-tooltip-left .arrow::before {
 border-left-color: $secondary-color !important;
}
.bs-tooltip-auto[x-placement^=right] .arrow::before, .bs-tooltip-right .arrow::before {
 border-right-color: $secondary-color !important;
}

#references {
 font-size: 0.8rem;
 word-break: break-all;
}

/* Progress bar */
#progress-bar {
	--scrollAmount: 0%;	
	// background-image: linear-gradient(120deg, $secondary-color 0%, $primary-color 100%);
	// width: var(--scrollAmount);

	background: linear-gradient(to right, $primary-color var(--scrollAmount), transparent 0);
	width: 100%;
	
	height: 4px;
	position: absolute;
 display:block;
	top: 0;
 z-index: 2000;
}
